@mixin btn($w,$bgc,$m,$p,$fz,$brd,$rad) {
  width: $w;
  background-color: $bgc;
  margin: $m;
  padding: $p;
  font-size: $fz;
  border: $brd;
  border-radius: $rad;
}

@mixin space-between() {
  display: flex;
  justify-content: space-between;
}

@mixin loginButtons($color) {
  @include btn(85%, $color, 10px, 10px, 27px, 2px solid $color, 5px);
}
  
@mixin postButtons($color) {
  @include btn(20%, $color, 3px, 3px, 18px, 2px solid $color, 5px);
}

@mixin createButton($color) {
  @include btn(8%, $color, 8px, 3px, 18px, 2px solid $color, 15px)
}

@mixin logOutButton($color) {
  @include btn(75%, $color, 0, 3px, 22px, 2px solid $color, 5px);
}

@mixin input {
  width: 90%;
  font-size: 27px;
  padding: 5px;
  margin-bottom: 15px;
  border: 2px $baseBlue solid;
  border-radius: 5px;
  color: $texcolor;

  &:focus {
    border: 3px $activeBlue solid;
    outline: none;
  }
}